import GenericPage from "./GenericPage";
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { CircularProgress } from '@material-ui/core';
import Pagination from '../components/Pagination';
import GenericCard from '../components/GenericCard';
import {Link} from  'react-router-dom';
import GeometricShapeComponent from '../components/GeometricShapeComponent';
import Button from '@material-ui/core/Button';

class MarketPlace extends GenericPage {
  constructor(props){
    super(props);
    this.state={
      value:0,
      isLoadingMarketplaceData:false,
      nfts:'',
      nftObjectList:[],
      listCounter:0,
      filteredList:[],
      currentIndex:0
    }
    // nfts = 
    // ids[]
    this.handleChange = this.handleChange.bind(this)
    this.getContentComponents = this.getContentComponents.bind(this)
    this.loadMarketplaceData = this.loadMarketplaceData.bind(this)
    this.getContentComponent = this.getContentComponent.bind(this)
    this.updateChosenNFT = this.updateChosenNFT.bind(this)
  }
  async handleChange(event, value) {
    this.setState({
      value,
      filteredList:[],
      currentIndex:0
    },()=>{this.loadMarketplaceData(value)})
  }
  async componentDidMount(){
    await this.loadMarketplaceData(0)
  }

  updateChosenNFT(event,value){
    this.setState({
      currentIndex:value-1
    })
  }

  getContentComponent(){
    return(
      <div className="marketplace-nft-component-content">
        {this.state.value >=0 && this.state.nftObjectList.length >0?        
        <>
          <div className="nft-container">
            <div className="no-padding">
            <GenericCard style={{maxWidth:"500px"}}>
              {/*ID:&nbsp;
         
              {this.state.nftObjectList[this.state.currentIndex].id}*/}
            
              <GeometricShapeComponent type={this.state.nftObjectList[this.state.currentIndex].id}/>

            </GenericCard></div>
            <br/>
            <Link to={"/detail/"+this.state.nftObjectList[this.state.currentIndex].id}>
              <Button>View details for NFT ID {this.state.nftObjectList[this.state.currentIndex].id}</Button>
            </Link>
          </div>
          
          <br/>
          <div className="nft-container">
            <Pagination callback={this.updateChosenNFT} count={this.state.nftObjectList.length}/>
          </div>
        </>
        :
        <div>
        Choose a filter above.
        </div>}
      </div>
    )
  }

  getContentComponents(){
    return (
      <div className="marketplace-nft-component">
        {this.state.isLoadingMarketplaceData?
        <CircularProgress/>:
          this.getContentComponent()
        }
      </div>
    )
  }

  async loadMarketplaceData(value){
      let nfts = ''
      let nftObjectList =[]

      try{
        this.setState({
          isLoadingMarketplaceData:true,nfts
        })
        nfts = await this.state.contractInstance.methods.findAllArt().call();
        for(let counter =0;counter<nfts[0].length;++counter){
          nftObjectList[counter]={
            id:nfts[0][counter],
            status:nfts[1][counter]
          }
        }
        if(value ===0){

          nftObjectList = nftObjectList.filter(function (x) {
            return x.status === "1"
          })
        }else if(value===1){
          nftObjectList = nftObjectList.filter(function (x) {
            return x.status === "0"
          })
        }
       
      }
      catch(e){
        console.log(e)
      }finally{
        this.setState({
          isLoadingMarketplaceData:false
        })

        this.setState({nfts,nftObjectList})
      }
  }

  render() {
    return(
      <GenericPage>
        <content>
        
          <Paper>
            <Tabs
              value={this.state.value}
              onChange={this.handleChange}
              indicatorColor="primary"
              textColor="inherit"
              centered
            >
              <Tab label="NFTs for sale" />
              <Tab label="NFTs not for sale" />
              <Tab label="All NFTs" />
            </Tabs>
          </Paper>
        </content>
        {this.getContentComponents()}

      </GenericPage>
    )
  }
}

export default MarketPlace;
