import React from 'react';
import AppNav from "../components/AppNav";
import AppFooter from "../components/AppFooter";
import PaperGrid from "../components/PaperGrid";
import Container from '@material-ui/core/Container';
import { CircularProgress } from '@material-ui/core';
import { getWeb3, getInstance}  from "../Web3Util";
import TokenComponent from '../components/TokenComponent';
import GeometricShapeComponent from '../components/GeometricShapeComponent';
import GenericCard from '../components/GenericCard';

class GenericPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      user: '',
      contractInstance: '',
      userHasDiamonds:false,
      isLoadingData:true,
      web3:'',
      fetchedNftIndex:-1
    };
  }

  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstance = await getInstance(web3);
      window.user = (await web3.eth.getAccounts())[0];

      const fetchedNftIndex = await contractInstance.methods.index().call();

      this.setState({ user: window.user,web3 });
      this.setState({ contractInstance: contractInstance,fetchedNftIndex });
      await this.loadData(web3);
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingData:false
      })
    }
  }
  
  async loadData() {
      try {
        const userHasDiamonds = await this.state.contractInstance.methods.senderHasDiamondAtLeastOneDiamond().call();

        if(userHasDiamonds){
          this.setState({ userHasDiamonds,hasData: true });
        }
    } catch (e) {
      console.log('Error', e)
    }
  }

  getPageComponent(loadingData, hasDiamonds,content){
    return(
      <div >
        <AppNav loadingData={loadingData} hasDiamonds={hasDiamonds}/>
        <section>
          <Container>
            <PaperGrid>
              {content}
            </PaperGrid>
          </Container>
        </section>
        <AppFooter/>
      </div>
    )
  }

  render() {

    if(this.state.isLoadingData){
      // add manually reload page button
      return this.getPageComponent(true,false,<CircularProgress />)
    } else if(this.state.userHasDiamonds){
      return this.getPageComponent(false,true,this.props.children)
    } else if(this.props.isPreview && (parseInt(this.props.previewId) < parseInt(this.state.fetchedNftIndex)) ){
      return this.getPageComponent(false,false,
        <div>   <br/> 
              {/*<img alt="Diamond" style={{maxWidth:"200px"}} src={diamondImage} />*/}
              <h1>NFT Preview</h1>
              <div style={{maxWidth:"350px",margin:"auto"}}>
                <GeometricShapeComponent type={this.props.previewId}/>
              </div>
              <br/>
              <br/>
          <br/>
          <b>This site is exclusive for diamond holders.</b>
          <br/><br/>
          In order to access this website you must hold at least one diamond token in your metamask or trust wallet.
          <br/><br/>
          Learn about the diamond token at <a href="https://diamondtoken.world" rel="noreferrer" target="_blank">diamondtoken.world</a>.
          <br/>
          <br/>
          <br/>
       
            
        </div>)
    }else if(!this.state.hasData) {
      return this.getPageComponent(false,false,
      <div>   <br/> 
            {/*<img alt="Diamond" style={{maxWidth:"200px"}} src={diamondImage} />*/}
            <TokenComponent/>
            <br/>
            <br/>
        <br/>
        <b>This site is exclusive for diamond holders.</b>
        <br/><br/>
        In order to access this website you must hold at least one diamond token in your metamask or trust wallet.
        <br/><br/>
        Learn about the diamond token at <a href="https://diamondtoken.world" rel="noreferrer" target="_blank">diamondtoken.world</a>.
        <br/>
        <br/>
        <br/>
     
          
      </div>)
    }
  }
}

export default GenericPage;
