import GenericPage from "./GenericPage";
import { CircularProgress } from '@material-ui/core';
import { getWeb3,getInstance}  from "../Web3Util";
import SimpleAccordion from '../components/SimpleAccordion';
import WalletTable from '../components/WalletTable';
import GenericSelect from '../components/GenericSelect';

import TransitionsModal from '../components/TransitionsModal';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


function createData(id,status, price, changePriceCallback,changeStatusCallback,transferNFTCallback,shapeColor,bgColor) {
  return { id, status, price, changePriceCallback,changeStatusCallback,transferNFTCallback,shapeColor,bgColor };
}

class HomePage extends GenericPage {

  constructor(props){
    super(props)
    this.state={
      balance:0,
      diamondBalance:0,
      isLoadingWalletData:false,
      myNFTs:[],
      modalOpen:false,
      tokenID:0,
      modalType:0,
      newPrice:0,
      newStatus:0,
      newRecipient:""
    }
    this.loadWalletData = this.loadWalletData.bind(this)
    this.changePriceCallback = this.changePriceCallback.bind(this)
    this.changeStatusCallback = this.changeStatusCallback.bind(this)
    this.transferNFTCallback = this.transferNFTCallback.bind(this)
    this.callbackCloseModal = this.callbackCloseModal.bind(this)
    this.getModalComponent = this.getModalComponent.bind(this)

    this.updateNewPrice = this.updateNewPrice.bind(this)
    this.updateNewRecipient = this.updateNewRecipient.bind(this)
    this.updateNewStatus = this.updateNewStatus.bind(this)

    this.confirmNewPrice = this.confirmNewPrice.bind(this)
    this.confirmNewRecipient = this.confirmNewRecipient.bind(this)
    this.confirmNewStatus = this.confirmNewStatus.bind(this)

    this.getChangePriceComponent= this.getChangePriceComponent.bind(this)
    this.getChangeRecipientComponent= this.getChangeRecipientComponent.bind(this)
    this.getChangeStatusComponent= this.getChangeStatusComponent.bind(this)
  }
  
  componentDidMount = async () => {
    const web3 = await getWeb3();
    const contractInstance = await getInstance(web3);

    window.user = (await web3.eth.getAccounts())[0];

    this.setState({web3,user:window.user,contractInstance})
    await this.loadWalletData()
  }

  loadWalletData = async()=>{
    let balance = 0
    let diamondBalance = 0
    let myNFTs = []
    try{
      this.setState({
        isLoadingWalletData:true
      })
      const balanceInWei = await this.state.web3.eth.getBalance(this.state.user);
      const myArts = await this.state.contractInstance.methods.findMyArts().call()
      diamondBalance = await this.state.contractInstance.methods.queryDiamondBalance().call()
      balance =  this.state.web3.utils.fromWei(balanceInWei, 'ether');

      for(let counter=0;counter<myArts.length;++counter){
        const currentNFT = await this.state.contractInstance.methods.findArtSimple(myArts[counter]).call()
        myNFTs[counter] = {
          id:currentNFT[0],
          price:currentNFT[1],
          status:currentNFT[2],
          changePriceCallback:this.changePriceCallback,
          changeStatusCallback:this.changeStatusCallback,
          transferNFTCallback:this.transferNFTCallback
        }
      }
    }
    catch(e){
      console.log(e)
    }finally{
      this.setState({
        isLoadingWalletData:false
      })
      this.setState({diamondBalance,myNFTs,balance});
    }
  }

  formatDiamondNumber(diamondCounter){
    let string=""
    for(let counter=0;counter<diamondCounter.length;++counter){
      string+=diamondCounter[counter]
  
      if(diamondCounter.length -10 === counter){
        string+="."
      }
    }

    return string
  }

  getWalletAddressComponent(balance,diamondBalance){
    return (
        <div>
          <div style={{float:"left",textAlign:"left"}}>
              <div>
                <b>{this.formatDiamondNumber(diamondBalance)}</b> 
              </div>
              <div>
              <div >
              <b>{balance} </b> 
              </div>
              </div>
          </div>
          <div style={{float:"right",textAlign:"right"}}>
              <div>
                 ðŸ’Ž
              </div>
              <div>
              <b>AVAX</b> 
              </div>
          </div>
        </div>
    );
  }

  async changeStatusCallback(tokenID){
    this.setState({
      modalOpen:true,
      tokenID,
      modalType:1
    })
  }
  async changePriceCallback(tokenID){
    this.setState({
      modalOpen:true,
      tokenID,
      modalType:0
    })
  }
  async transferNFTCallback(tokenID){
    this.setState({
      modalOpen:true,
      tokenID,
      modalType:2
    })
  }

  callbackCloseModal(){
    this.setState({
      modalOpen:false,
      newPrice:0,
      newStatus:0,
      newRecipient:""
    })
  }

  updateNewPrice(event){
    this.setState({
      newPrice:event.target.value
    })
  }

  updateNewRecipient(event){
    this.setState({
      newRecipient:event.target.value
    })
  }

  updateNewStatus(event){
    this.setState({
      newStatus:event.target.value
    })
  }

  async confirmNewPrice(){
    try{
      this.setState({
        isLoadingWalletData:true
      })
      const priceInWei =  this.state.web3.utils.toWei(this.state.newPrice, 'ether');
      await this.state.contractInstance.methods.changeArtPrice(this.state.tokenID,priceInWei).send({
      })
    }
    catch(e){
      // console.log(e)
    }finally{
      this.setState({
        modalOpen:false
      })
      await this.loadWalletData()
    }
  }

  async confirmNewRecipient(){
    try{
      this.setState({
        isLoadingWalletData:true
      })
      await this.state.contractInstance.methods.transfer(this.state.newRecipient,this.state.tokenID).send({
      })
    }
    catch(e){
      // console.log(e)
    }finally{
      this.setState({
        modalOpen:false
      })
      await this.loadWalletData()
    }
  }

  async confirmNewStatus(){
    let currentTokenStatus
 
    for(let counter=0;counter<this.state.myNFTs.length;++counter){
      if(this.state.myNFTs[counter].id+"" === this.state.tokenID+""){
        currentTokenStatus = this.state.myNFTs[counter].status
      }
    }
    if(currentTokenStatus+"" === this.state.newStatus+""){
      this.setState({
        modalOpen:false
      })
      return;
    }
   
    try{
      this.setState({
        isLoadingWalletData:true
      })
      await this.state.contractInstance.methods.changeArtStatus(this.state.tokenID,this.state.newStatus).send({
      })
    }
    catch(e){
      // console.log(e)
    }finally{
      this.setState({
        modalOpen:false
      })
      await this.loadWalletData()
    }
  }

  getChangeRecipientComponent(tokenID){
    return(
      <div style={{width:"100%"}}>
        <h3>
          Transfer NFT ID {tokenID}
        </h3>
        <TextField onChange={this.updateNewRecipient} style={{width:"100%"}} label="Recipient address" type="text" /> &nbsp;
        <br />
        <Button style={{float:"right"}} onClick={this.confirmNewRecipient}>Confirm</Button>
      </div>
    )
  }
  getChangePriceComponent(tokenID){
    return(
      <div style={{width:"100%"}}>
        <h3>
          Change price for NFT ID {tokenID}
        </h3>
        <TextField onChange={this.updateNewPrice} style={{width:"100%"}} label="New price (in AVAX)" type="number" /> &nbsp;
        <br />
        <Button style={{float:"right"}} onClick={this.confirmNewPrice}>Confirm</Button>
      </div>
    )
  }

  getChangeStatusComponent(tokenID){
   
    return(
      <div style={{width:"100%"}}>
        <h3>
          Change status for NFT ID {tokenID}
        </h3>
        <GenericSelect callback={this.updateNewStatus}  />
        <br />
        <Button style={{float:"right"}} onClick={this.confirmNewStatus}>Confirm</Button>
      </div>
    )
  }


  getModalComponent(){
    return(
      <>
        {this.state.modalType===-1?
        <h1>Closing window...</h1>
        :null}
        {this.state.modalType===0?
          this.getChangeStatusComponent(this.state.tokenID)
        :null}
        {this.state.modalType===1?
          this.getChangePriceComponent(this.state.tokenID)
        :null}
        {this.state.modalType===2?
           this.getChangeRecipientComponent(this.state.tokenID)
        :null}
      </>
    )
  }

  getBackgroundColor= (id) =>{
    let colorColorBG = Math.floor((Math.abs(Math.sin(id-123321) * 16777215)) % 16777215).toString(16);
    colorColorBG = "#"+colorColorBG
    return colorColorBG
  }

   getShapeColor= (id)=>{
    let color = Math.floor((Math.abs(Math.sin(id) * 16777215)) % 16777215).toString(16);
    color = "#"+color
    return color
  }

  render() {
    let rows = [
    ]
    for(let counter=0;counter<this.state.myNFTs.length;++counter){
      rows.push(createData(
        Number.parseInt(this.state.myNFTs[counter].id),
        this.state.myNFTs[counter].status,
        this.state.web3.utils.fromWei(this.state.myNFTs[counter].price, 'ether'),
        this.state.myNFTs[counter].changeStatusCallback,
        this.state.myNFTs[counter].changePriceCallback,
        this.state.myNFTs[counter].transferNFTCallback,
        this.getShapeColor(this.state.myNFTs[counter].id),
        this.getBackgroundColor(this.state.myNFTs[counter].id)))
    }
    return(
      <GenericPage>
        {this.state.isLoadingWalletData?
          <CircularProgress/>
        :<>
        <content>
          <SimpleAccordion
            title0={"Wallet address"}
            title1={"Wallet ballance"}
            content1={this.getWalletAddressComponent(this.state.balance,this.state.diamondBalance)}
            content0={this.state.user}
          />
          <WalletTable rows={rows}/>
        </content>
        <TransitionsModal  callbackCloseModal={this.callbackCloseModal} isOpen={this.state.modalOpen} >
            {this.getModalComponent()}
        </TransitionsModal>
      </>}
      </GenericPage>
    )
  }
}

export default HomePage;
