import React, { useRef,useEffect,  useState , useMemo} from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Math as ThreeMath, FrontSide } from 'three'
import * as THREE from 'three';

import { OrbitControls} from '@react-three/drei'

const geometries = ['octahedron', 'icosahedron', 'octahedron', 'dodecahedron', 'tetrahedron']

function convertBase(str, fromBase, toBase) {

  const DIGITS = "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ+/";

  const add = (x, y, base) => {
      let z = [];
      const n = Math.max(x.length, y.length);
      let carry = 0;
      let i = 0;
      while (i < n || carry) {
          const xi = i < x.length ? x[i] : 0;
          const yi = i < y.length ? y[i] : 0;
          const zi = carry + xi + yi;
          z.push(zi % base);
          carry = Math.floor(zi / base);
          i++;
      }
      return z;
  }

  const multiplyByNumber = (num, x, base) => {
      if (num < 0) return null;
      if (num == 0) return [];

      let result = [];
      let power = x;
      while (true) {
          num & 1 && (result = add(result, power, base));
          num = num >> 1;
          if (num === 0) break;
          power = add(power, power, base);
      }

      return result;
  }

  const parseToDigitsArray = (str, base) => {
      const digits = str.split('');
      let arr = [];
      for (let i = digits.length - 1; i >= 0; i--) {
          const n = DIGITS.indexOf(digits[i])
          if (n == -1) return null;
          arr.push(n);
      }
      return arr;
  }

  const digits = parseToDigitsArray(str, fromBase);
  if (digits === null) return null;

  let outArray = [];
  let power = [1];
  for (let i = 0; i < digits.length; i++) {
      digits[i] && (outArray = add(outArray, multiplyByNumber(digits[i], power, toBase), toBase));
      power = multiplyByNumber(fromBase, power, toBase);
  }

  let out = '';
  for (let i = outArray.length - 1; i >= 0; i--)
      out += DIGITS[outArray[i]];

  return out;
}

function GeometricShape(props) {
  // This reference will give us direct access to the mesh
  let tempType =0
  let stringTemp = convertBase(props.type+"",10,5)
  if(props.type>0){
    tempType =stringTemp[stringTemp.length-1] 
  }else{
    tempType=0
  }
  let color = Math.floor((Math.abs(Math.sin(props.type) * 16777215)) % 16777215).toString(16);

  const mesh = useRef(() => `${geometries[tempType]}BufferGeometry`, [tempType])
  const Geometry = useMemo(() => `${geometries[tempType]}BufferGeometry`, [tempType])
  const [init, setInit] = useState(false)

  // Set up state for the hovered and active state
  const [hovered, setHover] = useState(false)
  // Rotate mesh every frame, this is outside of React without overhead
  useFrame(() => {
    mesh.current.rotation.x = mesh.current.rotation.y += 0.01
  })
  useEffect(() => {
    setInit(true)
    return () => {
      // console.log('cleanup!')
    }
  }, [])
  if(color == 0){
    color = "ffffff"
  }

  return (
    <mesh
      {...props}
      ref={mesh}>
         <Geometry attach="geometry" args={[0.5, 0]} />
          <meshPhysicalMaterial attach="material" color={"#"+color} opacity={1} metalness={0} roughness={0.5} reflectivity={1} side={FrontSide} /> 
    </mesh>
  )
}

export default function GeometricShapeComponent(props) {
  let colorColorBG = Math.floor((Math.abs(Math.sin(props.type-123321) * 16777215)) % 16777215).toString(16);
  let localColor = new THREE.Color('#'+colorColorBG)
  return (
    <Canvas style={{height:"250px"}} camera={{ fov: 15, position: [0, 0, 5] }}>
      <ambientLight intensity={0.5} />
      <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} />
      <pointLight position={[-10, -10, -10]} />
      <GeometricShape type={props.type} position={[0, 0, 0]} />
      <OrbitControls enableZoom={false} />
      <color attach="background" args={[ localColor]} />

    </Canvas>
  )
}
