import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import HomeIcon from '@material-ui/icons/Home';
import AccountBalanceWalletIcon from '@material-ui/icons/AccountBalanceWallet';
import { LinearProgress } from '@material-ui/core';
import {Link} from "react-router-dom"
import StorefrontIcon from '@material-ui/icons/Storefront';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function AppNav(props) {
  const classes = useStyles();


  return (
    <div className={classes.root}>
      <AppBar color="inherit" position="fixed">
        {props.loadingData?
         <LinearProgress style={{marginBottom:"-5px"}}/>
        :null}

       <Toolbar>
         {props.hasDiamonds?
         <>
          <Typography variant="h6" className={classes.title}>
            <Link to="/"><Button color="inherit"> Homepage&nbsp;<HomeIcon/></Button></Link>
          </Typography>
          <Typography variant="h6" className={classes.title}>
          <Link to="/marketplace"><Button color="inherit"> Marketplace&nbsp;<StorefrontIcon/></Button></Link>
          </Typography>
         
         <Link to="/wallet"><Button color="inherit">Wallet &nbsp; <AccountBalanceWalletIcon/></Button></Link>
         </>
        :
      
        <>
          {!props.loadingData?
            <>
              <Typography variant="h6" className={classes.title}>
                <Button color="inherit">Exclusive content</Button>
              </Typography>
              <a href="https://diamondtoken.world" rel="noreferrer" target="_blank"> <Button color="inherit">ðŸ’Ž</Button></a>
            </>
            :null
            }
        </>
        }

        </Toolbar> 
      </AppBar>
    </div>
  );
}