import GenericPage from "./GenericPage";
import {getInstance,getWeb3}  from "../Web3Util";
import { CircularProgress } from '@material-ui/core';
import GenericCard from '../components/GenericCard';
import {Link} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import TokenComponent from '../components/TokenComponent';
import GeometricShapeComponent from '../components/GeometricShapeComponent';


class HomePage extends GenericPage {
  constructor(props) {
    super(props);
    this.state = {
      hasHomepageData: false,
      isLoadingHomepageData:true,
      nftIndex:-1,
      maxArts:-1
    };
    this.mintNft = this.mintNft.bind(this);
  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstance = await getInstance(window.web3);
      this.setState({ user: window.user });
      this.setState({ contractInstance: contractInstance });
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingHomepageData:false
      })
    }
  }
  
  async loadData() {
      try {
        const nftIndex = await this.state.contractInstance.methods.index().call();
        const maxArts = await this.state.contractInstance.methods.MAX_ARTS().call();
        if(nftIndex){
          this.setState({ nftIndex,hasHomepageData: true  });
        }
        if(maxArts){
          this.setState({ maxArts,hasHomepageData: true  });
        }
    } catch (e) {
      console.log('Error', e)
    }
  }

  async mintNft(){
    try {
     
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstance = await getInstance(window.web3);
      const priceInWei =  window.web3.utils.toWei("0.25", 'ether');
      this.setState({
        isLoadingHomepageData:true
      })
      await contractInstance.methods.createToken().send({
          value: priceInWei
      })

    } catch (e) {
      console.log('Error', e)
    }
    finally{
      await this.loadData()
      this.setState({
        isLoadingHomepageData:false
      })
    }
  }

  getLastNFTComponent(){
    if(this.state.isLoadingHomepageData){
      return(
        <CircularProgress />
      )
    }
     else if(this.state.nftIndex >0){
      return(
        <>
          <h3>
            Latest NFT minted out of {this.state.maxArts}
          </h3> <br/>
          <div className={"no-padding"}>
          <GenericCard style={{maxWidth:"500px"}} >
            {/*ID:&nbsp;{this.state.nftIndex -1}*/}
            <GeometricShapeComponent type={this.state.nftIndex -1}/>

          </GenericCard></div>
          <br/>
          <Link to={"/detail/"+(this.state.nftIndex-1)}>
           <Button> View details for NFT ID {this.state.nftIndex-1}</Button>
          </Link>
        </>
      )
    } 
    else if(this.state.nftIndex === "0"){
      return(
        <>
          <h2>
            Be the first to mint a NFT!
          </h2>
        </>
      )
    } 
    return null;
  }

  render() {
    return(
      <GenericPage>
        <content>
         
          {this.state.nftIndex === this.state.maxArts?
         null
        :
        <>
          <TokenComponent/>
          <br/>
          <p>
            <b>{this.state.nftIndex} NFTs minted so far</b>
            <br/>
            <b>{this.state.maxArts-this.state.nftIndex} NFTs left to be minted</b>
            <br/>
            <br/>
            <span> <Button onClick={this.mintNft} style={{textTransform:"initial"}} >Click here</Button> to mint your ðŸ’Ž NFT</span> for 0.25 AVAX
          </p>
          <br/>
        </>
        }
         
          {this.getLastNFTComponent()}
        
        </content>
      </GenericPage>
    )
  }
}

export default HomePage;
