import GenericPage from "./GenericPage";
import { getWeb3, getInstance}  from "../Web3Util";
import { CircularProgress } from '@material-ui/core';
import GenericCard from '../components/GenericCard';
import SimpleAccordionSingle from '../components/SimpleAccordionSingle';
import GeometricShapeComponent from '../components/GeometricShapeComponent';

import Button from '@material-ui/core/Button';

class DetailPage extends GenericPage {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingDetailData:true,
      owner:'',
      currentArt:{},
      currentArtTxs:{},
      nftIndex:-1,
      fetchedNftIndex:-1
    };
    this.loadDetailData = this.loadDetailData.bind(this)
    this.buyNFT = this.buyNFT.bind(this)
  }
 
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      
      window.web3 = web3;
      const contractInstance = await getInstance(window.web3);
      this.setState({ user: window.user });
      this.setState({ contractInstance: contractInstance });
     
      await this.loadDetailData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingDetailData:false
      })
    }
  }
  
  async loadDetailData() {
      try {
        this.setState({
          isLoadingDetailData:true
        })
        const currentArt = await this.state.contractInstance.methods.findArtSimple(this.props.match.params.id).call();
        const currentArtTxs = await this.state.contractInstance.methods.getArtAllTxn(this.props.match.params.id).call();
        const ownerOf = await this.state.contractInstance.methods.ownerOf(this.props.match.params.id).call();
        const nftIndex = await this.state.contractInstance.methods.index().call();
        this.setState({currentArt,ownerOf,currentArtTxs,nftIndex})

    } catch (e) {
      console.log('Error', e)
    }
    finally{
      this.setState({
        isLoadingDetailData:false
      })
    }
  }

  async buyNFT(){
    try {
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstance = await getInstance(window.web3);
      this.setState({
        isLoadingDetailData:true
      })
      await contractInstance.methods.buyArt(this.props.match.params.id).send({
         value: this.state.currentArt[1]
      })
     
    } catch (e) {
      console.log('Error', e)
    }
    finally{
      await this.loadDetailData()
    }
  }

  getTransactionsComponent(transactionsObject){
    let transactionData = []
    for(let counter =0;counter<transactionsObject[0].length;++counter){
      transactionData.push(
        <div key={counter} >
          <hr />
          <h5>
            transaction #{counter+1}
          </h5>
          Seller: {transactionsObject.seller[counter]} <br/>
          Buyer: {transactionsObject.buyer[counter]}<br/>
          Sold for: {window.web3.utils.fromWei(transactionsObject[1][counter]+"", 'ether')} AVAX <br/>
          
        </div>
      )
    }
    transactionData.reverse()
    return(
      <div>
        {transactionData}
      </div>
    )
  }

  render() {
    let color = Math.floor((Math.abs(Math.sin(this.props.match.params.id) * 16777215)) % 16777215).toString(16);
    color = "#"+color
    let colorColorBG = Math.floor((Math.abs(Math.sin(this.props.match.params.id-123321) * 16777215)) % 16777215).toString(16);
    colorColorBG = "#"+colorColorBG
    
    return(
      <GenericPage isPreview={true} previewId={this.props.match.params.id}>
        <content>
        {this.state.isLoadingDetailData?
        
        <CircularProgress />
      
        :
        <>{this.state.nftIndex>=0?
        
          <>
          <h1 style={{fontWeight:"500"}}>
           NFT ID: {this.props.match.params.id}</h1><br/>
           <div className="no-padding">
          <GenericCard style={{maxWidth:"500px"}}>
              {/*ID:&nbsp;{this.props.match.params.id} */}
              <GeometricShapeComponent type={this.props.match.params.id}/>
          </GenericCard></div>
          <br/>
        
          <p>
            <b>Current owner: {this.state.ownerOf}</b>
          </p>
  
          <p>
            <b>Shape color: <span style={{color}}>{color.toUpperCase()}</span></b>
          </p>
  
          <p>
            <b>Background color: <span style={{color:colorColorBG}}>{colorColorBG.toUpperCase()}</span></b>
          </p>
          
          {(this.state.currentArt[2] ==='1')?
            <>
            {window.web3 !== undefined && this.state.currentArt[1]>0?
            <p>
            <b>Current price:  {window.web3.utils.fromWei( this.state.currentArt[1], 'ether')} AVAX
            </b>
          </p>
          :null}
            </>
          :null}
  
          {(this.state.currentArt[2] ==='0')?
              <p><b>
              Not for sale
              </b></p>
          :null}
         
          
            {window.user !== undefined?
            <div>
              {(window.user !== this.state.ownerOf) &&
              (this.state.currentArt[2] ==='1')?
              <Button onClick={this.buyNFT} style={{marginBottom:"15px"}}>
                Buy now
              </Button>
                :null
            }
            </div>
          :null}
  
            {this.state.currentArtTxs[0].length >0?
              <SimpleAccordionSingle title={"Trasaction history"} content={this.getTransactionsComponent(this.state.currentArtTxs)} />
            :null
            }

            <Button onClick={() => {navigator.clipboard.writeText(window.location.href)}}>Click here to copy share link</Button>
        
          </>
        
        :
        <h1>
          NFT not found
        </h1>
        }</>
        
        }
       </content>
      </GenericPage>
    )
  }
}

export default DetailPage;
